@import '../../styles/variables';

.Reviews {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #d9d9d9;
  & > .wrapper-form {
    background-color: white;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .Results {
    background-color: white;
    width: 100%;
    display: flex;
    justify-content: center;
    & > .wrapper {
        width: 1320px;
        display: flex;
        flex-direction: column;
        padding: 60px 0px;
        gap: 20px;
        & > .topBlock {
            display: flex;
            flex-direction: column;
            gap: 20px;
            .tab {
                background-color: rgba(217, 217, 217, 1);
                width: 108px;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 20px;
                span {
                    color: $primary;
                    font-weight: 400;
                    font-size: 15px;
                    letter-spacing: -4%;
                }
              }
      
              h2 {
                font-size: 64px;
                font-weight: 500;
                color: black;
                line-height: 95%;
                letter-spacing: -4%;
                span {
                    font-family: "Arapey", serif;
                    font-style: italic;
                }
              }
        }
        .bottomBlock {
            width: 100%;
            height: auto;
            border-radius: 16px;
            display: flex;
            justify-content: space-between;
            overflow: hidden;
            align-items: flex-end;
            gap: 20px;
          
            .compareBlock {
              width: 50%;
              height: 520px; // ✅ высота слайдера
              border-radius: 16px;
              overflow: hidden;
              box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
          
              .react-compare-slider {
                width: 100%;
                height: 100%; // ✅ занимает всю высоту блока
                border-radius: 16px;
              }
          
              .react-compare-slider-image {
                width: 100%;
                height: 100%;
                object-fit: cover;
              }
            }
            & > button {
                background-color: $primary;
                color: $white;
                border-radius: 20px;
                height: 100px;
                outline: none;
                border: none;
                font-weight: 400;
                font-size: 24px;
                width: 50%;
                cursor: pointer;
                letter-spacing: -4%;
                transition: all 0.3s;
                &:hover {
                    background-color: rgba(217, 217, 217, 1);
                    color: $primary;
                }
            }
        }    
        .lastBlock {
            display: flex;
            gap: 20px;
            align-items: center;
            & > .circle {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: rgba(243, 245, 246, 1);
                &.active {
                    background-color: rgba(78, 54, 41, 1);
                }
            }
        }          
    }
}
}
.reviews-header {
  text-align: left;
  margin-bottom: -40px;
  max-width: 1320px;
  width: 100%;
  padding-top: 60px;
  @media (max-width: 768px) {
    margin-bottom: -40px;
  }
  .tag {
    display: inline-block;
    background: #fff;
    color: #3a281e;
    padding: 8px 30px;
    border-radius: 30px;
    font-size: 15px;
  }

  .title {
    color: #3a281e;
    font-size: 64px;
    font-weight: 700;
    line-height: 0.95;
    padding-top: 20px;
    @media (max-width: 768px) {
      font-size: 36px;
    }
  }

  .subtitle {
    font-family: "Arapey", serif;
    font-style: italic;
    font-weight: 400;
    font-size: 70px;
    color: #3a281e;
    @media (max-width: 768px) {
      font-size: 42px;
    }
  }
}
